
import styled from 'styled-components'

import Layout from './components/layout'

export default function Blog() {



    return (
        <Layout>

            <Head>
                <title>Blog</title>
                <link rel="icon" href="/h.png" />
            </Head>

            <Center>
                <Anim src={animation} />
            </Center>
        </Layout>
    )

}

const Anim = styled.img`
    width: 320px;
    height: 320px;
`